/**
 * ItemPrice
 * @param {*} Price 
 */

export default (Price) => {
    let zhuan = parseInt(Price / 100 / 100 / 10000);
    let jin = parseInt(Price / 100 / 100 % 10000);
    let yin = parseInt(Price / 100 % 100);
    let tong = parseInt(Price % 100);

    let output = '';
    if (zhuan) output += zhuan + '';
    if (jin) output += jin + '';
    if (yin) output += yin + '';
    output += tong + '';

    return output;
}

