<template>
    <div class="m-breadcrumb">
        <div class="u-stat">
            <span v-for="(value, key) in countDict" :key="key">
                <em>{{ value }}</em>
                <b>{{ count[key] }}</b>
            </span>
        </div>
    </div>
</template>
<script>
import { get_items_count } from "@/service/item.js";

export default {
    name: "ItemBreadcrumb",
    props: [],
    data: function () {
        return {
            count: {},
            countDict: {
                total: "",
                quality_0: "",
                quality_1: "",
                quality_2: "",
                quality_3: "",
                quality_4: "",
                quality_5: ""
            },
        };
    },
    computed: {
        client() {
            return this.$store.state.client;
        }
    },
    methods: {},
    mounted: function () {
        // 
        get_items_count({client: this.client}).then((res) => {
            res = res.data;
            this.count = res.data;
        });
    },
};
</script>

<style lang="less">
@import "~@/assets/css/item/bread.less";
</style>
